.anniversary-nudge-container {
  background-color: #fe7523;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% - 20px);
  border-radius: 8px;
  margin: 10px;
  box-shadow: -2px 2px 13px 0 rgba(210, 210, 210, 0.3); }

@media only screen and (min-width: 1310px) {
  .anniversary-nudge-container.inside-content {
    display: none !important; }
  .anniversary-nudge-container.outside-content {
    transform: translateY(10px);
    max-width: 400px; }
    .anniversary-nudge-container.outside-content .anniversary-gif {
      height: 165px; } }

@media only screen and (max-width: 1309px) {
  .anniversary-nudge-container.outside-content {
    display: none !important; } }
