.modal-overlay-div {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.3); }

.modal-content-div {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10000;
  overflow: auto;
  text-align: left;
  cursor: pointer; }

.modal-content-div::after {
  display: inline-block;
  height: 100%;
  margin-left: -0.05em;
  content: "";
  vertical-align: middle; }

.modal-dialog-div {
  position: relative;
  outline: 0;
  width: auto;
  display: inline-block;
  box-sizing: border-box;
  cursor: default;
  background-color: white;
  vertical-align: middle;
  max-width: auto; }

@media only screen and (min-width: 420px) {
  .modal-dialog-div {
    margin-top: 100px;
    margin-bottom: 50px;
    padding-top: 30px;
    padding-right: 30px;
    padding-left: 30px; } }

@media only screen and (max-width: 560px) {
  @-webkit-keyframes slideInUp {
    from {
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
      visibility: visible; }
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }
  @keyframes slideInUp {
    from {
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
      visibility: visible; }
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }
  .modal-dialog-div {
    vertical-align: bottom;
    width: 100%;
    -webkit-animation-name: slideInUp;
    animation-name: slideInUp;
    padding-top: 15px;
    padding-right: 15px;
    padding-left: 15px;
    overflow: hidden;
    animation-timing-function: ease-in-out;
    -webkit-animation-duration: 0.5s;
    animation-duration: 0.5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both; }
  .modal-content-div {
    top: 15%; }
  .modal-children {
    animation: 0.6s fade-in;
    animation-timing-function: ease-in-out; }
    .modal-children .btn-custom {
      width: 100% !important; }
  @keyframes fade-in {
    0% {
      opacity: 0; }
    50% {
      opacity: 0.5; }
    100% {
      opacity: 1; } } }

@media only screen and (min-width: 992px) {
  .modal-dialog-div {
    left: 30%;
    max-width: 60%; } }

@media only screen and (max-width: 991px) and (min-width: 560px) {
  .modal-dialog-div {
    left: 10%;
    width: 80%; } }

.modal-heading {
  font-weight: 700;
  font-size: 18px;
  padding-bottom: 15px;
  padding-top: 15px;
  color: #576574; }

.modal-description {
  font-size: 17px;
  color: #848f99;
  padding-bottom: 30px; }
