.timeline {
  overflow: hidden;
  padding: 0; }
  .timeline .timeline-container {
    padding: 0 10px; }
    .timeline .timeline-container .not-last-item-after {
      content: '';
      position: absolute;
      background: #576574;
      width: 1px;
      height: 65px;
      top: 20px;
      left: 14px;
      z-index: 0; }
    .timeline .timeline-container .timeline-content-wrapper {
      z-index: 1;
      align-items: center;
      display: flex; }
    .timeline .timeline-container .timeline-block {
      display: flex;
      z-index: 1;
      margin-bottom: 20px; }
    .timeline .timeline-container .timeline-marker {
      width: 30px;
      min-width: 30px;
      height: 30px;
      z-index: 1;
      background: white;
      border: 1px solid #576574;
      color: #576574;
      border-radius: 50%;
      display: flex;
      padding: 6px;
      justify-content: center;
      align-self: center;
      align-items: center;
      line-height: 0; }
    .timeline .timeline-container .timeline-img {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0; }
    .timeline .timeline-container .timeline-content {
      flex-grow: 1;
      padding: 10px;
      position: relative; }
