.snudge-card-container {
  color: #ffffff;
  position: relative; }
  .snudge-card-container .snudge-container {
    padding-left: 20px;
    padding-right: 20px; }
  .snudge-card-container .snudge-content-container {
    padding: 15px 0 10px 0; }
  .snudge-card-container p {
    margin: 0; }
  .snudge-card-container .title {
    font-size: 20px;
    padding-bottom: 5px; }
  .snudge-card-container .description {
    font-size: 16px;
    color: #8e8c8e; }
  .snudge-card-container .snudge-label {
    background-color: #143158;
    font-size: 12px;
    margin-top: 10px;
    min-width: 120px;
    min-height: 26px;
    display: flex;
    align-items: center;
    margin-left: -5px;
    padding: 5px 25px 5px 25px;
    color: #ffffff; }
  .snudge-card-container .snudge-label-shadow {
    width: 0;
    border-right: 3px solid #cccccc;
    border-top: 3px solid #cccccc;
    border-left: 2px solid transparent;
    border-bottom: 3px solid transparent;
    margin-top: 17px;
    margin-left: -25px;
    position: absolute; }
  .snudge-card-container .snudge-button-container {
    padding: 0 20px 20px; }
  .snudge-card-container .snudge-button {
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    display: block;
    width: 100%;
    color: #FFFFFF;
    background-color: #10ac84;
    border: none;
    padding: 10px;
    border-radius: 8px; }
    .snudge-card-container .snudge-button:focus, .snudge-card-container .snudge-button:active {
      background-color: #25b38f; }
    .snudge-card-container .snudge-button:hover {
      background-color: #0e8d6d; }
